@import url(https://fonts.googleapis.com/css?family=Montserrat|Zilla+Slab&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
p {
  margin: 0px;
}
h2,
h3 {
  margin: 0px;
}
h2 {
  font-family: Zilla Slab;
  font-style: normal;
  font-weight: normal;
  font-size: 33px;
  /* or 117% */

  text-align: center;

  color: #ffffff;
}
h3 {
  font-family: Montserrat;
  font-style: normal;
  font-weight: normal;
  font-size: 18px;
  text-align: center;
  color: #cbcfd4;
}

.App-header {
  background-color: #30363d;
  text-align: center;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}
.progress {
  width: 100px;
}
.CircularProgressbar-path {
  box-shadow: 0px 10px 10px rgba(0, 0, 0, 0.25);
}
.CircularProgressbar-trail {
  stroke: gray;
}
.CircularProgressbar-text {
  fill: transparent;
}
.Title {
  text-align: left;
  font-family: Zilla Slab;
  font-style: normal;
  font-weight: normal;
  font-size: 24px;
  line-height: 28px;
  /* identical to box height, or 117% */

  color: #ffffff;
}

.normal {
  background: #3c444d !important; }

.card {
  background: -webkit-gradient(linear, left top, right top, from(#f27a54), to(#a154f2));
  background: -webkit-linear-gradient(left, #f27a54 0%, #a154f2 100%);
  background: linear-gradient(90deg, #f27a54 0%, #a154f2 100%);
  border-radius: 4px;
  -webkit-transition: box-shadow 300ms cubic-bezier(0.4, 0, 0.2, 1) 0ms;
  transition: box-shadow 300ms cubic-bezier(0.4, 0, 0.2, 1) 0ms;
  box-shadow: 0px 1px 3px 0px rgba(0, 0, 0, 0.2), 0px 1px 1px 0px rgba(0, 0, 0, 0.14), 0px 2px 1px -1px rgba(0, 0, 0, 0.12);
  color: rgba(0, 0, 0, 0.87);
  padding: 20px;
  text-align: center;
  min-width: 80vw;
  margin: 10px 0;
  -webkit-transition: all 1000ms linear;
  transition: all 1000ms linear; }

.button-domo {
  -webkit-text-decoration: transparent;
          text-decoration: transparent;
  width: 100%;
  margin: auto;
  text-rendering: auto;
  color: #ffffff;
  letter-spacing: normal;
  word-spacing: normal;
  text-transform: none;
  text-indent: 0px;
  text-shadow: none;
  display: inline-block;
  text-align: center;
  align-items: flex-start;
  cursor: default;
  background-color: transparent;
  box-sizing: border-box;
  font-family: Montserrat;
  padding: 10px;
  border: 1px solid #ffffff;
  border-radius: 20px;
  font-family: Zilla Slab;
  font-style: normal;
  font-weight: normal;
  font-size: 33px;
  text-align: center;
  -webkit-transition: all 1s;
  transition: all 1s; }

.button-domo:focus {
  background: #ffffff4d;
  outline: none; }

.button-domo:hover {
  background: #ffffff23;
  outline: none; }

.buttonStyle {
  color: transparent;
  background: transparent;
  border: 2px solid transparent;
  border-radius: 20px;
  cursor: default;
  margin: 8px;
  -webkit-transition: all 0.7s;
  /* Safari prior 6.1 */
  transition: all 0.7s; }

.EmojiStyle {
  margin: 14px; }

.buttonStyle:hover {
  border: 2px solid #ffffff8e;
  box-sizing: border-box; }

.buttonStyle:focus {
  border: 2px solid #ffffff;
  outline: none; }

.coverActive {
  -webkit-filter: grayscale(0%);
  /* Safari 6.0 - 9.0 */
  filter: grayscale(0%); }

.coverDeactivated {
  -webkit-filter: grayscale(100%);
  /* Safari 6.0 - 9.0 */
  filter: grayscale(100%); }

img {
  -webkit-transition: -webkit-filter 1s;
  transition: -webkit-filter 1s;
  transition: filter 1s;
  transition: filter 1s, -webkit-filter 1s; }


.list-container {
  display: grid;
  grid-template-columns: 50px auto 60px;
  grid-gap: 10px;
  text-align: left;
  padding: 19px 0px; }

.list-who {
  display: grid;
  grid-template-rows: auto auto; }

.list-name {
  font-family: Montserrat;
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  line-height: 22px;
  /* identical to box height, or 157% */
  color: #ffffff; }

.list-artist {
  font-family: Zilla Slab;
  font-style: italic;
  font-weight: normal;
  font-size: 17px;
  line-height: 22px;
  /* identical to box height, or 129% */
  color: #cbcfd4; }

.valence {
  font-family: Zilla Slab;
  color: #cbcfd4; }

.list-audio {
  grid-column-start: 1; }
  .list-audio audio {
    text-align: center;
    width: 100%; }

.disabled-audio {
  grid-column-end: 3; }

.active-audio {
  grid-column-end: 4; }

.add-music {
  text-rendering: auto;
  color: white;
  cursor: default;
  background-color: transparent;
  box-sizing: border-box;
  margin: 0em;
  font-family: Zilla Slab;
  padding: 1px 6px;
  border-width: 2px;
  border-style: solid;
  border-color: white;
  border-radius: 100%;
  -webkit-transition: all 0.7s;
  transition: all 0.7s;
  outline: none; }

.add-music:hover {
  border: 2px solid #ffffff8e;
  box-sizing: border-box;
  outline: none; }

.add-music:active {
  border: 2px solid #ffffff;
  box-sizing: border-box;
  outline: none; }

